import React, { useEffect, useState } from "react";
import { graphql, Link } from "gatsby";

import NavBar from "../components/navbar";
import MobileNav from "../mobileComponents/mobileNav";
import MobileFooter from "../mobileComponents/mobileFooter";
import "../components/contactComponents/contact.css";
import "../mobileComponents/mobileFooter.css";

const Contact = ({ data }) => {

    const [formSubmitted, setFormSubmitted] = useState(false);
    const [submittedText, setSubmittedText] = useState(<p className="contactDeniedText">Reload the Page and Try Again!</p>);

    useEffect(() => {
        const element = document.getElementById("contactBullet");
        element.classList.add("filled");
    })

    const submitForm = () => {
        window.event.preventDefault();
        const reasonVal = document.getElementById("contactReason").value;
        const firstNameVal = document.getElementById("contactFirst").value;
        const lastNameVal = document.getElementById("contactLast").value;
        const emailVal = document.getElementById("contactFormEmail").value;
        const textBoxVal = document.getElementById("contactTextBox").value;

        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                contactReason: reasonVal,
                contactFirst: firstNameVal,
                contactLast: lastNameVal,
                contactFormEmail: emailVal,
                contactTextBox: textBoxVal
            })
        };

        fetch("https://sitesbyjack.com/contactSubmit", requestOptions)
            .then((response) => {
                if (!response.ok) {
                    throw Error(response.statusText);
                }
                return(response.json())
            })
            .then((responseJson) => {
                console.log(responseJson.data)
                if (responseJson.data == "successful post request and retrieval") {
                    setSubmittedText(<p className="contactSubmittedText">Form Submitted!</p>);
                }
            })
            .catch((error) => {
                console.log(error);
            });

        setFormSubmitted(true);
        
        document.getElementById("contactReason").value = "";
        document.getElementById("contactFirst").value = "";
        document.getElementById("contactLast").value = "";
        document.getElementById("contactFormEmail").value = "";
        document.getElementById("contactTextBox").value = "";
    }

    const submitButton = (<button type="submit" className="contactSubmit" onClick={() => submitForm()}>SUBMIT</button>);

    
    return (
        <>
            <section className="contactCont">
                <section className="contactLeft">
                    <section className="contactDescriptionCont">
                        <p className="contactDescription">
                            Please fill out this form and I will get back to you within one business day!
                        </p>
                    </section>
                </section>
                <section className="contactRight">
                    <form className="contactForm">
                        <select required id="contactReason" defaultValue="" name="contactReason" className="contactReason">
                            <option value="" disabled>Reason for Contacting</option>
                            <option value="newSite">New Site Needed</option>
                            <option value="siteMaintenance">Site Maintenance Needed</option>
                            <option value="other">Other</option>
                        </select>
                        <section className="contactFormNameInputs">
                            <input className="contactFirst" type="text" name="contactFirst" id="contactFirst" placeholder="First Name"></input>
                            <input className="contactLast" type="text" name="contactLast" id="contactLast" placeholder="Last Name"></input>
                        </section>
                        <input className="contactFormEmail" type="email" name="contactFormEmail" id="contactFormEmail" placeholder="Email"></input>
                        <textarea name="contactTextBox" id="contactTextBox" rows="10" cols="30" className="contactTextBox" maxLength="500" placeholder="Leave Your Message Here"></textarea>
                        {formSubmitted ? submittedText : submitButton}
                    </form>
                </section>
                <NavBar />
            </section>
            <MobileNav backgroundImage={data.siteLogoDark} currentPage="Contact" />
            <MobileFooter currentPage="Contact" backToTop={true} />
        </>
    );
}

export const query = graphql`
    query MyContactQuery {
        siteLogoDark: file(relativeDirectory: {eq: "homeImages"} name: {eq: "SitesByJackLogo Dark No Background Smaller"})
        {
            childImageSharp {
                gatsbyImageData(width: 500 placeholder: BLURRED formats: [AUTO, WEBP, AVIF])
            }
        }
    }
`

export default Contact;